@import "assets/styles/colors.scss";
@import "assets/styles/mixins.scss";
// @import "assets/styles/global.scss";
@import "assets/styles/variables.scss";

.card-container {
  font: $fontMainRegularS;
  @include textGrid;
  justify-items: center;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  align-items: center;
  color: $blackColor;
  gap: 1vw;
}
