.card-main-container {
  font: $fontMainRegularM;
  @include textGrid;
  gap: 1vw;
  padding: 15px 10px;
  border-bottom: 1px solid rgb(215, 227, 248);
  width: 100%;
  box-sizing: border-box;
  background-color: $secondColor;
  color: $blackColor;
  justify-items: center;
  align-items: center;
  text-align: center;
}

.added-word {
  width: 60%;
  border-radius: 10px;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.added-word-error {
  width: 60%;
  border-radius: 10px;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: $errorColor;
}
.added-word-error {
  border: 3px solid $errorColor;
}
